  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 0.5rem;
  overflow: hidden;
}
.sxa-accordion .accordion-item {
  border-bottom: 1px solid var(--border-color);
}
.sxa-accordion .accordion-item:last-child {
  border-bottom: none;
}
.sxa-accordion .accordion-header {
  margin: 0;
}
.sxa-accordion .accordion-button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 18px 24px;
  background: var(--bg-surface);
  border: none;
  text-align: left;
  cursor: pointer;
  transition: all 0.15s ease-in-out;
}
.sxa-accordion .accordion-button:hover {
  background: var(--bg-elevated);
