.sxa-accordion .accordion-button[aria-expanded=true] .accordion-title {
  color: var(--color-primary);
  font-weight: 600;
}
.sxa-accordion .accordion-button:focus-visible {
  outline: 2px solid var(--color-primary);
  outline-offset: -2px;
}
.sxa-accordion .accordion-title {
  flex-grow: 1;
  font-size: 16px;
  font-weight: 400;
  color: var(--text-primary);
  transition: all 0.15s ease-in-out;
}
.sxa-accordion .accordion-icon {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 15px;
  font-size: 20px;
  font-weight: 700;
  color: var(--color-primary);
  transition: all 0.15s ease-in-out;
}
.sxa-accordion .accordion-panel {
  max-height: 0;
